declare function require(params:string): any;

export class App{
  public log: string;
  public html: any;
  constructor(){
    this.html = require('./../../html/app.html');
    this.log = "ERROR 404 AMI NOT FOUND !";
  }

  public getLog(){
    return this.log
  }

  public getHtml(){
    return this.html;
  }
}


