import '../css/main.css';
import { App } from './components/app';

class Main{
  public app: App;
  constructor(){
    this.app = new App();
    console.error(this.app.getLog());
    $('#loading').remove();
    $('#app').append(this.app.getHtml());
  }
}

new Main();


